@mixin flexCenter {
   display: flex;
   justify-content: center;
   align-items: center;
  }

  //aspect ratio mixin
  @mixin aspect-ratio($width, $height) {
      position: relative;
      overflow: hidden;
      &:before {
        content: "";
        display: block;
        width: 100%;
        padding-top: ($height / $width) * 100%;
      }
      > * {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
      }  
      > img {
        height: 100%;
        object-fit: cover;
      }
    }
    
    // Mixin for removing hover efects on iphone screen
    @mixin hover-supported {    
      @media not all and (pointer: coarse) {
          &:hover {
              @content;
          }
      }
    }
  